.new-item__controls {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new-item__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new-item__control input {
  font: inherit;

  padding: 0.5rem;
  border-radius: 12px;
  border: 1px solid #cccccc;
  width: 20rem;
  max-width: 100%;
}

.new-item__control input:focus {
  outline: none;
}
.new-item__actions {
  text-align: right;
}
